/*effect-underline*/
a.effect-underline:after {
	content: '';
  position: absolute;
  left: 0;
  display: inline-block;
  height: 1em;
  width: 100%;
  border-bottom: 1px solid;
  margin-top: 10px;
  opacity: 0;
	transition: opacity 0.35s, transform 0.35s;
	transform: scale(0,1);
}

a.effect-underline:hover:after {
  opacity: 1;
	transform: scale(1);
}



p, dd, li{
  color: #808080;
  text-align: justify;
}

@media only screen and (max-width: 576px) {
  p, dd, li, span{
    font-size: 0.9rem;
    text-align: start;
  }

}

.carousel-control-next, .carousel-control-prev{
  bottom: 40px;
}
.carousel-indicators{
  bottom: -40px;
}

.carousel-indicators li{
  width: 10px;
  height: 10px;
  background-color: #808080;
  opacity: 0.1;
}

.my-shuffle .shuffle-text-char {
  -webkit-animation: 0.01s ease-in-out both myWordEnter;
          animation: 0.01s ease-in-out both myWordEnter; /* none for no effect */
}
 
@-webkit-keyframes myWordEnter {
  0% {
    opacity: 0;
    transform: translate(0, 0) scale(0.7);
  }
 
  100% {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
}
 
@keyframes myWordEnter {
  0% {
    opacity: 0;
    transform: translate(0, 0) scale(0.7);
  }
 
  100% {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
}


